import React, { Component } from "react";
import { connect } from "react-redux";
import { Form, InputGroup, FormControl, Button } from "react-bootstrap";
import actions from "../actions";
import { skills } from "../constants";
import SkillPill from "../components/SkillPill";

class SkillPicker extends Component {
    constructor() {
        super();
        this.state = {
            skill: ""
        }
        this.addSkill = this.addSkill.bind(this);
    }

    addSkill() {
        const skill = skills.find(item => item.level.toLowerCase() === this.state.skill.toLowerCase());
        if (skill) {
            this.props.add(skill);
            this.setState({skill:""});
        }
    }

    render() {
        const optionsList = [];
        skills.forEach(skill => {
            optionsList.push(<option key={skill.level} value={skill.level}>{skill.level}</option>);
        });
        const skillList = this.props.skills.map((skill, index) => <SkillPill key={index} onRemove={() => this.props.remove(skill.level)} skill={skill}/>);
        return (
            <div style={{marginBottom:"12px"}}>
                <div className="bordered" style={{padding:"12px"}}>
                    <div style={{display:"flex", alignItems:"center", marginBottom:"6px"}}>
                      <b style={{flex:"1", marginRight:"4px", marginTop:"4px", color:"#555"}}>Skills Picker:</b>
                      <i className="fa fa-trash" style={{color:"#777", marginRight:"6px", cursor:"pointer"}} onClick={this.props.clear}/>
                    </div>
                    <Form autoComplete="off" onSubmit={e => {
                        e.preventDefault();
                        this.addSkill();
                    }}>
                        <InputGroup>
                            <FormControl
                                list="skills" 
                                className="large-font"
                                value={this.state.skill}
                                onChange={e => this.setState({skill: e.target.value})}
                                placeholder="Search Skills"/>
                            <datalist id="skills">
                                {optionsList}
                            </datalist>
                            <InputGroup.Button>
                                <Button
                                  style={{height:"34px"}}
                                  disabled={this.state.skill.trim() === ""}
                                  onClick={this.addSkill}>
                                    <i className="fa fa-plus" style={{marginTop:"4px", color:"inherit"}}/>
                                </Button>
                            </InputGroup.Button>
                        </InputGroup>
                    </Form>
                    <hr style={{margin:"12px 0"}}/>
                    {
                      skillList.length > 0 ? skillList : <div style={{color:"#999", textAlign:"center"}}>No Skills Added</div>
                    }
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        skills: state.calculator.skills
    }
}

const mapDispatchToProps = dispatch => {
    return {
        add: skill => {
            dispatch(actions.addSkill(skill));
        },
        remove: skill => {
            dispatch(actions.removeSkill(skill));
        },
        clear: () => {
            dispatch(actions.setCalculatorState({skills:[]}));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SkillPicker);



