import React, { Component } from "react";

class SkillPill extends Component {
    constructor() {
        super();
        this.state = {
            hover: false
        };
        this.handleMouseHover = this.handleMouseHover.bind(this);
    }

    handleMouseHover(value) {
        this.setState({hover:value});
    }

    render() {
        let modifiers
        if (this.props.skill.name === "Critical Element") {
            modifiers = "Affinity affects elemental damage"
        } else if (this.props.skill.name === "Bludgeoner") {
            modifiers = "Raises attack as sharpness depletes";
        } else {
            modifiers = Object.keys(this.props.skill.modifiers).map(key => {
                return (key + ": +" + this.props.skill.modifiers[key] + " ");
            });
        }

        return (
            <div className="bordered"
                onMouseEnter={() => this.handleMouseHover(true)}
                onMouseLeave={() => this.handleMouseHover(false)}
                style={{display:"flex", padding:"6px 10px", marginTop:"6px", boxShadow:"0 1px 1px rgba(0,0,0,0.1)"}}>
              <div style={{flex:"1"}}>
                  {this.props.skill.level}<br/>
              {
                  modifiers.length === 0 ? null :
                    <small color="#777">{modifiers}</small>
              }
              </div>
              {
                  this.state.hover ?
                    <i className="fa fa-times"
                        style={{color:"#777", cursor:"pointer"}}
                        onClick={() => this.props.onRemove()}/> : null
              }
            </div>
        );
    }
}

export default SkillPill;



